* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  box-sizing: border-box;
  height: 46px;
  color: #444;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  touch-action: manipulation;
  vertical-align: top;
  background-color: #fff;
  border: 1px solid #fff;
  border-radius: 50px;
  outline: none;
  margin: 0 15px 15px 0;
  padding: 0 25px;
  font-size: 14px;
  font-weight: 600;
  line-height: 46px;
  text-decoration: none;
  transition: box-shadow .2s;
  display: inline-block;
  box-shadow: 0 4px 6px rgba(65, 132, 144, .1), 0 1px 3px rgba(0, 0, 0, .08);
}

button:focus-visible {
  border: 1px solid #4c51f9;
  outline: none;
}

button:hover {
  transition: all .2s;
  box-shadow: 0 7px 14px rgba(65, 132, 144, .1), 0 3px 6px rgba(0, 0, 0, .08);
}

button:active {
  background-color: gray;
}

button:disabled {
  color: #444;
  cursor: not-allowed;
  background-color: gray;
  border-color: #eee;
}

